rtiva, es la de marcar los mensajes desde la propia perspectiva, utilizando frases que comiencen con “Yo” en lugar de “Tú”. Esto evita que el otro se sienta culpable o atacado. Por ejemplo, decir “Yo me siento frustrado cuando se interrumpen mis ideas” en lugar de “Tú siempre interrumpes mis ideas” ayuda a comunicar la incomodidad sin acusación.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Expresión de necesidades y límites claros"}},[_c('p',[_vm._v("La comunicación asertiva requiere que las personas expresen sus necesidades de manera específica, estableciendo límites claros. Esto ayuda a evitar confusiones y permite a la otra parte, entender exactamente qué es lo que se necesita para resolver la situación. Establecer límites también facilita que la otra persona respete el espacio emocional o físico del interlocutor.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Técnica de &quot;Banco de Niebla&quot;"}},[_c('p',[_vm._v("Esta estrategia implica reconocer y validar los sentimientos del otro, sin ceder a presionar ni comprometer los propios valores. Por ejemplo, en un desacuerdo, responda con “Entiendo que sientas que esto es importante; sin embargo, mantengo mi punto de vista porque si consideras que…” permite que la conversación avance, respetando ambas posturas.")])])])],1)]),_vm._m(3),_c('p',{staticClass:"mb-lg-5",attrs:{"data-aos":"fade-right"}},[_vm._v("La asertividad también contribuye al desarrollo personal, dado que permite que las personas se sientan empoderadas y seguras al expresar sus opiniones, sin temor a ser ignoradas o desestimadas. Este empoderamiento fortalece la autoconfianza y motiva a las personas a establecer límites saludables en sus relaciones, lo que es fundamental para su bienestar emocional.")]),_vm._m(4),_vm._m(5),_vm._m(6),_c('h3',{attrs:{"data-aos":"fade-right"}},[_vm._v("Componentes fundamentales de la escucha activa")]),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("La escucha está compuesta por varios elementos claves que ayudan a garantizar que el receptor no solo capte el contenido verbal, sino también el contexto emocional y las señales no verbales del interlocutor:")]),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/slider-1.png")}})])]),_c('div',{staticClass:"col-lg-7"},[_c('h5',[_vm._v("Atención plena")]),_c('p',[_vm._v("La verdadera manera de escuchar comienza con la atención total hacia el interlocutor, eliminando distracciones y manteniendo el contacto visual. pEsto implica poner en pausa cualquier actividad paralela y centrarse exclusivamente en la otra persona.")]),_c('p',[_vm._v("Según La Rosa y Rivas (2017), “la atención plena demuestra respeto y disposición, lo cual genera confianza y facilita el entendimiento mutuo” (p. 45).")]),_c('p',[_vm._v("Esta completa atención permite captar tanto el mensaje, como el tono emocional detrás de las palabras.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/slider-2.png")}})])]),_c('div',{staticClass:"col-lg-7"},[_c('h5',[_vm._v("Parafraseo")]),_c('p',[_vm._v("Una técnica clave es el párrafo, que consiste en reformular lo que ha expresado la otra persona, para confirmar que se ha comprendido el mensaje. ")]),_c('p',[_vm._v("Al parafrasear, el oyente puede decir frases como: “Si entiendo bien, lo que me dices es…”.")]),_c('p',[_vm._v("Esta técnica no solo demuestra que se ha escuchado con atención, sino que también permite corregir cualquier malentendido de inmediato, evitando que el conflicto se intensifique por interpretaciones erróneas (PNUD, 2018).")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5"