n de soluciones equitativas.")]),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Conozcamos cada uno de ellos:")]),_c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-7"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Identificación de intereses comunes"}},[_c('p',[_vm._v("Un paso importante en el trabajo conjunto, es reconocer los intereses compartidos que pueden servir como base para construir soluciones. En lugar de centrarse exclusivamente en las posiciones individuales, es necesario buscar los objetivos comunes que cada parte tiene y que pueden alinearse. Esto no solo facilita el diálogo, sino que también aumenta la disposición de los involucrados, para encontrar alternativas que beneficien a todos (PNUD, 2018).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Promoción del compromiso y la flexibilidad"}},[_c('p',[_vm._v("La colaboración implica que cada parte esté dispuesta a realizar concesiones para alcanzar una solución satisfactoria. Esta flexibilidad es fundamental, dado que permite que todos los involucrados sientan que están recibiendo algo de valor, incluso si han tenido que ceder en ciertos aspectos. Al comprometerse y mostrar apertura al cambio, se fomenta un ambiente en el que las partes están motivadas a mantener el acuerdo alcanzado (La Rosa & Rivas, 2017).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Búsqueda de alternativas creativas"}},[_c('p',[_vm._v("En un proceso colaborativo, la generación de ideas innovadoras es esencial para encontrar soluciones que no solo resuelvan el conflicto, sino que también beneficien a ambas partes. Este enfoque promueve la creatividad y permite que los participantes se sientan empoderados, porque pueden aportar ideas y propuestas que enriquecen el diálogo. En situaciones complejas, como en el ámbito laboral o comunitario, la creatividad puede abrir nuevas posibilidades que antes no habían sido contempladas (UNITEC, 2016).")])]),_c('div'