ener noreferrer"}},[_vm._v("Linzoain, P (22 de marzo de 2022) Modelo Harvard De Negociación, Técnicas De Negociación #Mediación Y Resolución De Conflictos (archivo de video) Youtube")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/link.svg")}}),_c('a',{attrs:{"href":"https://organizacioneshumanas.org/blog/11-practicos-pasos-hacia-dinamicas-del-poder-mas-sanas-en-el-trabajo-mas-empowerment-menos-coercion-richard-d-bartlett-2/","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Barlett, R. (8 de julio de 2010) 11 Prácticos Pasos hacia Dinámicas del Poder más sanas en el trabajo Más empowerment, menos coerción. (blog)")])])]),_vm._m(14)])])])])],1)}
