ido (González Rey, 2016; UNITEC, 2016).")])]),_c('div',{staticClass:"col-lg-4 order-1 order-lg-2"},[_c('figure',[_c('img',{staticClass:"mb-4 mb-lg-0",attrs:{"src":require("@/assets/curso/tema3/6.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-7 order-lg-1 order-2"},[_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("La escucha activa aporta numerosos beneficios en la resolución de conflictos, promoviendo un ambiente en el que las tensiones pueden abordarse de manera constructiva. Uno de los principales beneficios es que se reduce la reactividad emocional. Al sentirse escuchada, la otra persona experimenta una disminución en la intensidad emocional, lo cual facilita el diálogo y evita que el conflicto escale. Cuando los participantes perciben que sus emociones y opiniones son respetadas, están más dispuestos a considerar las perspectivas ajenas (PNUD, 2018).")]),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Asimismo, se fortalecen las relaciones entre las partes, dado que permite construir un vínculo basado en la confianza y el respeto. En un entorno laboral, por ejemplo, los equipos que practican los componentes, pueden resolver desacuerdos de manera más rápida y eficiente, promoviendo un ambiente de colaboración y apoyo. En contextos familiares o personales, la escucha activa fomenta la empatía, lo cual es clave para resolver conflictos, sin dañar las relaciones (UNITEC, 2016).")])]),_c('div',{staticClass:"col-lg-5 order-1 order-lg-2"},[_c('figure',[_c('img',{staticClass:"mb-4 mb-lg-0",attrs:{"src":require("@/assets/curso/tema3/7.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-7 order-lg-2"},[_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Otra ventaja es que se pueden identificar intereses y necesidades subyacentes. Cuando se capta tanto el contenido como las emociones de la conversación, es posible descubrir las verdaderas preocupaciones detrás de las posturas iniciales, lo cual facilita la búsqueda de soluciones que respondan a las necesidades reales de cada parte. Esto es especialmente útil en conflictos complejos, donde las posiciones iniciales pueden ocultar intereses que aún no han sido expresados de manera abierta (González Rey, 2016).")]),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Escuchar de manera correcta, es una herramienta poderosa en la resolución de conflictos, pues permite un nivel de comprensión profundo que va más allá de las palabras. Mediante técnicas como la atención plena, el párrafo, la reflexión de sentimientos y las preguntas clarificadoras, esta habilidad no solo facilita el entendimiento mutuo, sino que también contribuye a reducir tensiones y construir relaciones de respeto y confianza. La escucha activa transforma el conflicto en una oportunidad de diálogo y aprendizaje, mejorando la disposición de las partes para encontrar soluciones que beneficien a todos.")])]),_c('div',{staticClass:"col-lg-5 order-lg-1"},[_c('figure',[_c('img',{staticClass:"mb-4 mb-lg-0",attrs:{"src":require("@/assets/curso/tema3/8.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_3_3","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("3.3")]),_vm._v(" Colaboración (cooperación)")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-8 order-2 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("La colaboración es una habilidad clave en la resolución de conflictos, porque implica el esfuerzo conjunto para encontrar soluciones que beneficien a ambas partes. Esta capacidad va más allá de la simple negociación, puesto que busca construir acuerdos en los que los involucrados se sientan reconocidos y satisfechos. La disposición para trabajar en equipo y valorar los intereses de los demás, es fundamental en esta dinámica, pues permite transformar el conflicto en una oportunidad para fortalecer las relaciones y construir confianza (González Rey, 2016; UNITEC, 2016).")])]),_c('div',{staticClass:"col-lg-4 order-1 order-lg-2"},[_c('figure',[_c('img',{staticClass:"mb-4 mb-lg-0",attrs:{"src":require("@/assets/curso/tema3/9.svg"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"mt-4 mt-lg-0",attrs:{"src":require("@/assets/curso/tema3/10.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"mb-4 mb-lg-0",attrs:{"src":require("@/assets/curso/tema3/11.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-7"},[_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("La colaboración es una habilidad esencial en la resolución de conflictos, debido a que permite que las partes transformen las tensiones en oportunidades de diálogo y fortalecimiento de relaciones. A través de la identificación de intereses comunes, la flexibilidad, la creatividad y el trabajo en equipo, la colaboración facilita la creación de soluciones sostenibles y mutuamente beneficiosas. Este enfoque no solo resuelve el conflicto actual, sino que también establece las bases para relaciones más respetuosas y constructivas a largo plazo, promoviendo un ambiente de confianza y crecimiento personal y colectivo (González Rey, 2016; UNITEC, 2016).")]),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("La aplicación de estos conocimientos es especialmente relevante en contextos donde la cooperación y el respeto mutuo son esenciales, como en el ámbito laboral, familiar o comunitario. La capacidad de resolver conflictos de manera pacífica, no solo mejora las relaciones, sino que también contribuye a un ambiente más armonioso y productivo en cualquier entorno. La unidad culmina con un enfoque integral que, al combinar teoría y práctica, sienta las bases para una resolución de conflictos eficaz y basada en el respeto y la comprensión, elementos clave para una convivencia pacífica y colaborativa (PNUD, 2018; UNITEC, 2016).")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"mb-4 mb-lg-0",attrs:{"src":require("@/assets/curso/tema3/12.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-12 col-md-6 col-lg-3 offset-lg-1"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/componentes/material-complementario.svg"),"alt":""}})])])}]

